html {
    height: 100%;
    box-sizing: border-box;
  }

  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }
body {
    position: relative;
    margin: 0;
    padding-bottom: 13rem;
    min-height: 100%;
    background-color: #f6f8f9;
}

.container {
    margin-top: 1em;
}


.container-img {
    background-image: url(/static/media/nodes-bg.5404b8c9.jpg);
    background-size: cover;
}

.container-img .container {
  padding: 3em;
  color: white;

}

.container-img .container p {
  margin-top: 6em;
  font-size: 1.1em;

}

.text-centered {
    width: 100%;
    text-align: center;
}

footer {

    color: white;
    text-align: center;
    margin-top: 1em;
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
}

.light-tile-footer {
    background-color: #343a40;
    padding: 1em;
}
.dark-tile-footer {
    background-color: #202326;
    padding: 2.5em;
}
.card {
    padding: 3%;
    text-align: justify;
    margin-bottom: 1.5em;
}

.card-body {
    padding: 0 1.25rem !important;
}

.card-deck {
    margin-bottom: 2em;
}

.col a:hover, .card-body a:hover {
    text-decoration: none !important;
}

.col a button {
    margin-bottom: 0.5em;
}

.card-header {
    margin-bottom: 0.5em;
}

.cardButton {
    float: right;
}

.productFormTitle {
    margin-bottom: 1em;
    margin-top: 2em;
    text-align: center;
}

.productFormSearch {
    margin-bottom: 5em;
    width: 80%;
    display: inline-block;
}

.ManageBusinessTitle{
  margin-bottom: 1em;
  margin-top: 1em;
  text-align: center;
}

.productFormSearch{
  margin-bottom: 5em;
  width: 80%;
  display: inline-block;
}

.searchButton{
    margin: 1.5%;
}

.productImage {
    width: 25%;
    float: left;
    margin-left: 1em;
    margin-right: 1em;
}

